import { useMemo } from 'react'
import { createHttpLink } from 'apollo-link-http'
import { setContext } from 'apollo-link-context'
import Amplify, { Auth } from 'aws-amplify'
// import PropTypes from 'prop-types'
import { endpoints, aws } from '../config'
import { ApolloLink } from 'apollo-link'
import { InMemoryCache, ApolloClient } from 'apollo-boost'

Amplify.configure(aws)

const LOCAL_STORAGE_TOKEN = 'apollo-token'

function useGraphQL () {
  const apolloClient = useMemo(createApolloClient, [])
  return apolloClient
}

export default useGraphQL

/**
 * Helper function to create our Apollo Client object
 * @param {*} tokenFunc
 */
const getCurrentToken = async () => {
  try {
    const currSession = await Auth.currentSession()
    return currSession.accessToken.jwtToken
  }
  catch (e) {
    console.error(e)
    return null
  }
}

function createApolloClient () {
  // Set up our headers for authentication through graphql
  const cache = new InMemoryCache()

  // The auth link is just used to decorate our HTTP headers with authentication data
  const authLink = setContext(async (_, { headers }) => {
    const token = await getCurrentToken()
    const newHeaders = {
      ...headers,
      authorization: token ? `Bearer ${token}` : ''
    }
    // on Dev machines, allow us to fake a user. This will ONLY work with a complimentary
    // Dev graphql endpoint and singin is still required.

    // This was done so we can test what the App looks like from varying perspectives
    if (token && process.env.NODE_ENV === 'development' && process.env.REACT_APP_FAKE_USER) {
      newHeaders.auth_user = process.env.REACT_APP_FAKE_USER
    }
    return { headers: newHeaders }
  })

  // HTTP link creates the network connection to our graphql host
  const httpLink = createHttpLink({ uri: endpoints.graphql })
  console.log('Creating Apollo Client Client', endpoints.graphql)

  const apolloClient = new ApolloClient({
    link: ApolloLink.from([authLink, httpLink]),
    tokenName: LOCAL_STORAGE_TOKEN,
    cache,
    connectToDevTools: process.env.NODE_ENV === 'development'
  })

  // Now put it all together
  return apolloClient
}

